.page-footer {
  display: flex;
  flex-direction: column;
  align-items: center;

  height: 284px;

  @include wrap();
}

.page-footer__logo-link {
  display: block;

  margin: auto;
  margin-top: 40px;

  color: $color;

  &[href]:hover,
  &[href]:focus {
    color: $call-to-action__color;

    outline: none;
  }

  &[href]:active {
    color: $color;

    opacity: 0.3;
  }
}

.page-footer__logo-svg {
  width: 147px;
  height: 40px;

  &--mobile {
    display: block;
  }

  &--desktop {
    display: none;
  }
}

.page-footer__social {
  margin: auto;
  margin-top: 20px;
}

.page-footer__copyright {
  margin: auto;
  margin-bottom: 40px;
}

.not-footer-mobile .page-footer {
  display: none;
}

@media (min-width: $tablet-width) {
  .page-footer,
  .not-footer-mobile .page-footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    height: 127px;
  }

  .page-footer__logo-link {
    margin: 0;
    transform: translateY(-10px);
  }

  .page-footer__social {
    margin: 0;
    padding-right: 19px;
  }

  .page-footer__copyright {
    margin: 0;
  }
}

@media (min-width: $desktop-width) {
  .page-footer,
  .not-footer-mobile .page-footer {
    max-width: $desktop-width;
    height: 197px;

    @include wrap--desktop();
  }

  .page-footer__logo-link {
    transform: translateY(-7px);
  }

  .page-footer__logo-svg {
    &--mobile {
      display: none;
    }

    &--desktop {
      display: inline-block;
    }
  }

  .page-footer__social {
    padding-right: 0;
    padding-left: 9px;
  }
}
